import { OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { Transaction } from '../transaction';
import { TransactionService } from '../transaction.service';
import { Product } from '../product';
import { Router } from '@angular/router';
var AddTransactionComponent = /** @class */ (function () {
    function AddTransactionComponent(transactionService, router) {
        this.transactionService = transactionService;
        this.router = router;
        this.transaction = new Transaction();
        this.names = [];
        this.hidden = [];
        this.i = 0;
    }
    AddTransactionComponent.prototype.onSubmit = function () {
        var _this = this;
        var sum = 0;
        for (var i = 0; i < this.transaction.product.length; i++) {
            this.transaction.product[i].totals = this.transaction.product[i].total();
            sum = sum + this.transaction.product[i].totals;
        }
        this.transaction.total = sum;
        this.transactionService.addTransaction(this.transaction).subscribe(function (transaction) {
            _this.transactionService.throwTransaction(transaction);
            _this.router.navigate(["transaction/show"]);
        });
    };
    AddTransactionComponent.prototype.showName = function () {
        if ((this.names.indexOf(this.i) > -1)) {
            this.i = this.i + 1;
            console.log(true);
        }
        var product = new Product();
        product.quantity = null;
        product.s_price = null;
        this.transaction.product[this.i] = product;
        this.hidden.push(false);
        this.names.push(this.i);
        if (this.i > 0) {
            this.hidden[this.i - 1] = true;
        }
        this.i = this.i + 1;
    };
    AddTransactionComponent.prototype.deleteName = function () {
        if (!(this.names.indexOf(this.i) > -1)) {
            this.i = this.i - 1;
            console.log(true);
        }
        this.transaction.product.pop();
        this.hidden.pop();
        this.hidden[this.i - 1] = false;
        this.names.pop();
        this.i = this.i - 1;
        //i can't be negative
        if (this.i < 0)
            this.i = 0;
    };
    AddTransactionComponent.prototype.ngOnChanges = function (changes) {
    };
    AddTransactionComponent.prototype.ngOnInit = function () {
        this.transaction.clientPhone = "";
        this.transaction.product = [];
    };
    return AddTransactionComponent;
}());
export { AddTransactionComponent };
