import { OnInit } from '@angular/core';
import { Product } from '../product';
import { ProductService } from '../product.service';
import { Router } from '@angular/router';
var AddProductComponent = /** @class */ (function () {
    function AddProductComponent(productService, router) {
        this.productService = productService;
        this.router = router;
        this.product = [];
        this.names = [];
        this.hidden = [];
        this.i = 0;
    }
    AddProductComponent.prototype.onSubmit = function () {
        this.productService.addProduct(this.product).subscribe();
        this.productService.throwProduct(this.product);
        this.router.navigate(["product/add"]);
    };
    AddProductComponent.prototype.showName = function () {
        if ((this.names.indexOf(this.i) > -1)) {
            this.i = this.i + 1;
            console.log(true);
        }
        var product = new Product();
        product.quantity = null;
        product.s_price = null;
        this.product[this.i] = product;
        this.hidden.push(false);
        this.names.push(this.i);
        if (this.i > 0) {
            this.hidden[this.i - 1] = true;
        }
        this.i = this.i + 1;
    };
    AddProductComponent.prototype.deleteName = function () {
        if (!(this.names.indexOf(this.i) > -1)) {
            this.i = this.i - 1;
            console.log(true);
        }
        this.product.pop();
        this.hidden.pop();
        this.hidden[this.i - 1] = false;
        this.names.pop();
        this.i = this.i - 1;
        //i can't be negative
        if (this.i < 0)
            this.i = 0;
    };
    AddProductComponent.prototype.ngOnInit = function () {
    };
    return AddProductComponent;
}());
export { AddProductComponent };
